#home {
    position: relative;
    background: url("../../assets/bgWhite.png");
    background-size: cover;
    background-repeat: repeat;
    background-position: center;
    .app__wrapper {
        padding: 0;
        .copyright {
            display: none;
        }
    }
}

.app__header {
    flex: 1;
    width: 100%;
    height: 100%;
    flex-direction: row;
    padding: 6rem 2rem 0;
    @media screen and (min-width: 2000px) {
        padding-top: 8rem;
    }
    @media screen and (max-width: 1200px) {
        flex-direction: column;
    }
    @media screen and (max-width: 450px) {
        padding: 6rem 1rem 2rem;
    }
}

.app__header-info {
    flex: 0.65;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    height: 100%;
    @media screen and (min-width: 2000px) {
        width: 100%;
        margin-right: 0rem;
    }
}

.app__header-badge {
    width: 100%;
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    flex-direction: column;
    .badge-cmp,
    .tag-cmp,
    .tag-cv {
        padding: 1rem 2rem;
        border: var(--white-color);
        border-radius: 15px;
        flex-direction: row;
        width: auto;
        letter-spacing: 1px;
        box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);
    }
    .tag-cmp {
        flex-direction: column;
        margin-top: 3rem;
        p {
            width: 100%;
            text-transform: uppercase;
            text-align: right;
        }
    }
    .tag-cv {
        flex-direction: column;
        margin-top: 3rem;
        a {
            width: 100%;
            text-transform: uppercase;
            text-align: right;
            text-decoration: none;
        }
    }
    span {
        font-size: 2.5rem;
        @media screen and (min-width: 2000px) {
            font-size: 5rem;
        }
    }
    @media screen and (max-width: 1200px) {
        justify-content: flex-start;
        align-items: flex-start;
    }
}

.app__header-circles {
    flex: 0.75;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: flex-start;
    height: 100%;
    margin-left: 1rem;
    div {
        width: 100px;
        height: 100px;
        border-radius: 50%;
        background: var(--darkGray-color);
        box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);
        img {
            width: 50%;
            height: 50%;
        }
    }
    div:nth-child(1) {
        width: 100px;
        height: 100px;
    }
    div:nth-child(2) {
        margin: 1.75rem;
        width: 125px;
        height: 125px;
    }
    div:nth-child(3) {
        width: 80px;
        height: 80px;
    }
    @media screen and (min-width: 2000px) {
        div:nth-child(1) {
            width: 400px;
            height: 400px;
        }
        div:nth-child(2) {
            width: 170px;
            height: 170px;
        }
        div:nth-child(3) {
            width: 200px;
            height: 200px;
        }
    }
    @media screen and (max-width: 1200px) {
        width: 100%;
        flex-direction: row;
        flex-wrap: wrap;
        margin-left: 0;
        div {
            margin: 1rem;
        }
    }
}

.app__header-img {
    flex: 1;
    height: 90%;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    img {
        width: 95%;
        object-fit: contain;
        z-index: 1;
    }
    .overlay_circle {
        position: absolute;
        left: 0;
        right: 0;
        bottom: 1rem;
        z-index: 0;
        width: 100%;
        height: 90%;
    }
    .overlay_blackCircle {
        position: absolute;
        left: 0;
        right: 0;
        bottom: 1rem;
        z-index: 0;
        width: 100%;
        height: 90%;
    }
    @media screen and (min-width: 2000px) {
        margin: 2rem 0;
    }
}